import React, { Component } from 'react';
import { StyleSheet, Text, View, StatusBar, Alert, TouchableOpacity, ScrollView, AppRegistry} from 'react-native';
import { StackNavigator } from 'react-navigation'
import Qx from './qx';
import Syxy from './Syxy';
import updateCheck from './updateCheck';
import Ysxy from './Ysxy';

class szym extends Component {
  render() {
    return (
      <>
        <StatusBar translucent={true} backgroundColor="transparent" barStyle="dark-content" />
        <ScrollView>
          <View style={styles.titleBar}>
            <Text style={styles.titleText}></Text>
            <Text style={styles.hitokoto}></Text>
          </View>
          <View style={styles.bodyBackground}>
            <View style={styles.bodyContent}>
              <View style={styles.optionGroup}>
                <TouchableOpacity style={styles.optionTextGroup} onPress={() => this.props.navigation.navigate('Syxy')}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.optionTextGroup} onPress={() => this.props.navigation.navigate('Ysxy')}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                  <Text style={styles.optionTextDescript}>(*/*)</Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.optionTextGroup} onPress={() => this.props.navigation.navigate('updateCheck')}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.optionTextGroup} onPress={() => this.props.navigation.navigate('Qx')}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.optionTextGroup} onPress={this.AboutMe.bind(this)}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                  <Text style={styles.optionTextDescript}>tzh894895@gmail.com</Text>
                </TouchableOpacity>
                <TouchableOpacity style={styles.optionTextGroup}>
                  <Text style={styles.optionText}></Text>
                  <Text style={styles.optionTextDescript}></Text>
                  <Text style={styles.optionTextDescript}>@@@@@Rominwolf@</Text>
                  <Text style={styles.optionTextDescript}></Text>
                </TouchableOpacity>
              </View>
            </View>
            <Text style={{ alignSelf: "center", marginBottom: 50, fontSize: 12, color: '#808080',fontWeight:'bold' }}>-  -</Text>
          </View>
        </ScrollView>
      </>
    );
  }
  AboutMe() {
    Alert.alert('', '(*/*)',
      [
        { text: "", onPress: this.confirm }
      ]
    );
  }
}
//StackNavigator
export default StackNavigator({
  Home: {
    screen: szym,
  },
  Syxy: {
    screen: Syxy,
  },
  Ysxy: {
    screen: Ysxy,
  },
  Qx: {
    screen: Qx,
  },
  updateCheck: {
    screen: updateCheck,
  },
},{
  headerMode:'None' //
});

const styles = StyleSheet.create({
  titleBar: {
    flex: 2,
    backgroundColor: '#E9E9E9',
  },
  titleText: {
    fontSize: 40,
    fontWeight: '600',
    marginLeft: 30,
    marginTop: 110
  },
  hitokoto: {
    marginTop: 20,
    marginLeft: 30,
    marginBottom: 20,
    fontSize: 20,
    fontWeight: 'bold',
    fontStyle: 'italic',
  },
  bodyBackground: {
    flex: 5,
    backgroundColor: '#E9E9E9',
  },
  bodyContent: {
    flex: 1,
    backgroundColor: '#FFFFFF',
    borderTopLeftRadius: 30,
    borderTopRightRadius: 30,
    borderBottomLeftRadius: 30,
    borderBottomRightRadius: 30,
    marginBottom:30
  },
  optionGroup: {
    marginTop: 30,
  },
  optionText: {
    fontSize: 20,
    marginLeft: 30,
    fontWeight: '800'
  },
  optionTextDescript: {
    marginLeft: 30,
  },
  optionTextGroup: {
    marginBottom: 30,
  },
})